import aline from '../../components/aline.vue';
export default {
  data() {
    return {
      globalUser: {},
      imgurl: 'http://dummyimage.com/100x100/f2a379&text=faceImages'
    };
  },
  onShow() {
    var _this = this;
    _this.globalUser = _this.getGlobalUser('globalUser');
  },
  methods: {
    clearStorage() {
      uni.clearStorage();
      uni.showToast({
        title: '清理缓存成功',
        mask: true,
        duration: 1500
      });
    },
    logout() {
      var _this = this;
      var globalUser = _this.getGlobalUser('globalUser');
      uni.request({
        url: _this.serverURL + '/user/logout?userId=' +
          globalUser.id,
        method: 'POST',
        success: res => {
          if (res.data.status == 200) {
            uni.removeStorageSync('globalUser');
            uni.switchTab({
              url: '../me/me'
            });
          }
        }
      });
    },
    operator() {
      var _this = this;
      var globalUser = _this.getGlobalUser('globalUser');
      uni.showActionSheet({
        itemList: ['查看头像', '上传头像'],
        success(res) {
          var index = res.tapIndex;
          if (index == 0) {
            //查看头像
            var faceArr = [];
            faceArr.push(globalUser.faceImages);
            uni.previewImage({
              urls: faceArr,
              current: faceArr[0]
            });
          } else if (index == 1) {
            //上传头像
            uni.chooseImage({
              count: 1,
              success(res) {
                //获得临时路径
                var tempFilePath = res.tempFilePaths[0];
                uni.navigateTo({
                  url: '../meFace/meFace?tempFilePath=' +
                    tempFilePath
                });
              }
            });
          }
        }
      });
    }
  },
  components: {
    aline
  }
};
